<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <h3 class="my-4"></h3>
    <!--  -->
    <table class="table mt-4">
      <thead>
        <tr>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
          <td></td>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in orders" :key="item.id">
          <td>{{item.num}}</td>
          <td>{{item.create_at |date}}</td>
          <td>
            <ul class="list-unstyled">
              <li>{{item.user.name}}</li>
              <li>{{item.user.tel}}</li>
              <li>{{item.user.email}}</li>
              <li>{{item.user.address}}</li>
            </ul>
          </td>
          <td>
            <ul class="list-unstyled">
              <li v-for="(product,i) in item.products" :key="i">
                {{product.product.title}} {{product.qty}}
                {{product.product.unit}}
              </li>
            </ul>
          </td>
          <td>{{item.total|currency}}</td>
          <td v-if="item.is_paid" class="text-success text-center"></td>
          <td v-else class="text-danger text-center"></td>
          <td class="text-center">
            <button class="btn btn-outline-primary btn-sm" @click="openEditOrderModal(item)"></button>

            <button class="btn btn-outline-primary btn-sm" @click="openDelOrderModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- Modal -->
    <div
      class="modal fade"
      id="editOrderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <h6></h6>
                  <label for="title"></label>
                  <input type="text" v-model="tempOrder.user.name" class="form-control" id="title" />
                  <label for="title"></label>
                  <input type="text" v-model="tempOrder.user.tel" class="form-control" id="title" />
                  <label for="title"></label>
                  <input type="text" v-model="tempOrder.user.email" class="form-control" id="title" />
                  <label for="title"></label>
                  <input
                    type="text"
                    v-model="tempOrder.user.address"
                    class="form-control"
                    id="title"
                  />
                </div>
                <hr />
                <div class="form-group">
                  <h6></h6>
                  <label for="title"></label>
                  <input
                    type="text"
                    v-model="tempOrder.total"
                    class="form-control"
                    id="order-total"
                  />

                  <label for="message"></label>
                  <textarea
                    v-model="tempOrder.message"
                    cols="5"
                    rows="3"
                    class="form-control"
                    id="message"
                  ></textarea>
                  <label class="form-check-label" for="is_paid"></label>
                  <input
                    type="checkbox"
                    v-model="tempOrder.is_paid"
                    class="form-check-input"
                    id="is_paid"
                    :true-value="1"
                    :false-value="0"
                  />
                </div>
              </div>
            </div>
          </div>

          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateOrder"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- Modal -->
    <div
      class="modal fade"
      id="delOrderModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger"> {{ tempOrder.num }}{{tempOrder.user.name}}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="removeOrder(tempOrder.id)"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="changePagination">
      <pagination :pagination="pagination" @pageTrigger="getOrders"></pagination>
    </div>
  </div>
</template>


<script>
import $ from "jquery";
import pagination from "@/components/pagination";

export default {
  components: {
    pagination
  },
  data() {
    return {
      pagination: {},
      isLoading: false,
      orders: [
        {
          create_at: "", //
          is_paid: false, //
          message: "", //
          paid_date: "", //
          payment_method: "credit_card",
          products: [], //
          total: "", //
          num: "", //
          user: {
            address: "", //
            email: "", //
            name: "", //
            tel: "" //
          }
        }
      ],
      tempOrder: {
        user: {
          address: "",
          email: "",
          name: "",
          tel: ""
        }
      }
    };
  },
  methods: {
    getOrders(page = 1) {
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/orders?page=${page}`;
      const vm = this;
      vm.isLoading = true;
      console.log(process.env.APIPATH, process.env.CUSTOMPATH);
      this.$http.get(api).then(response => {
        vm.isLoading = false;
        vm.orders = response.data.orders;
        vm.pagination = response.data.pagination;
      });
    },
    openEditOrderModal(item) {
      this.tempOrder = item;
      $("#editOrderModal").modal("show");
    },
    updateOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`; //

      this.$http.put(url, { data: vm.tempOrder }).then(response => {
        if (response.data.success) {
          vm.getOrders();
          $("#editOrderModal").modal("hide");
        } else {
          console.log(response);
        }
      });
    },
    openDelOrderModal(item) {
      this.tempOrder = item;
      $("#delOrderModal").modal("show");
    }
    // removeOrder(id) {
    //   const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/order/${vm.tempOrder.id}`;
    //   const vm = this; //vm
    //   vm.isLoading = true;
    //   console.log(process.env.APIPATH, process.env.CUSTOMPATH);
    //   this.$http.delete(api).then(response => {
    //     $("#delOrderModal").modal("hide");
    //     vm.getOrders();
    //   });
    // }
  },
  created() {
    this.getOrders();
  }
};
</script>
<style scoped>
.form-check-input {
  margin-left: 13px;
}
</style>


